.btn-flip {
  opacity: 1;
  color: #ff6b09;
  text-align: center;
  letter-spacing: 1px;
  text-transform: capitalize;
  background-color: rgba(16, 33, 54, .8);
  border-radius: 34px;
  outline: 0;
  padding-left: 10px;
  padding-right: 10px;
  font-family: Open Sans;
  line-height: 40px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.btn-flip:hover {
  cursor: pointer;
}

.btn-flip:hover:after {
  opacity: 1;
  transform: translateY(0) rotateX(0);
}

.btn-flip:hover:before {
  opacity: 0;
  transform: translateY(50%)rotateX(90deg);
}

.btn-flip:after {
  opacity: 0;
  width: 100%;
  color: #323237;
  content: attr(data-back);
  background: #adadaf;
  border-color: #adadaf;
  transition: all .5s;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-50%)rotateX(90deg);
}

.btn-flip:before {
  opacity: 1;
  color: #adadaf;
  content: attr(data-front);
  transform: translateY(0) rotateX(0);
  background: #323237;
  border-color: #adadaf;
  padding: 0 30px;
  line-height: 40px;
  transition: all .5s;
  display: block;
  position: relative;
  top: 0;
  left: 0;
}

